import React, { useState, useEffect, useRef } from "react";
import { Item, Button } from "semantic-ui-react";
import {
  DiscoverConsumer,
  ConfigConsumer,
  RecommendedPdtsContainer,
  RecommendedPdtsConsumer,
} from "../../container";
import { getCookie } from "../../common";
import { HeaderVueContent } from "../../configs";
import {
  ListElement,
  CardBlock,
  ProductCarousel,
  ProductCarouselWithTabs,
  Input,
} from "../../component";
import {
  NoItemsMessage,
  ParentWrapper,
  ProductWrapper,
  CardWrapper,
  DescriptionWrapper,
  DescriptionChildWrapper,
  ButtonWrapper,
  DeliveryWrapper,
  StyledLink,
  ProductRecommendationWrapper,
  ProductRecommendationTitle,
} from "./styled";
import { HeaderConfigConsumer } from "../../container";

const buy_now = (update_items_to_display) => {
  //localStorage.removeItem("cart_items");
  window.location.href = "/order_confirmation";
};

const clear_cart = (update_items_to_display) => {
  localStorage.removeItem("cart_items");
  update_items_to_display([]);
};

const RenderCartItems = (props) => {
  let all_items_in_cart = localStorage.getItem("cart_items");
  const [is_loading, update_loading] = useState(true);
  const [items_to_display, update_items_to_display] = useState([]);
  let temp_items = [];
  if (
    all_items_in_cart != undefined &&
    all_items_in_cart != "" &&
    all_items_in_cart != "{}" &&
    is_loading
  ) {
    update_loading(false);
    all_items_in_cart = JSON.parse(all_items_in_cart);
    for (let key in all_items_in_cart) {
      console.log(key);
      let _obj = {};
      _obj["childKey"] = key;
      _obj["title"] = all_items_in_cart[key].title;
      _obj["meta"] = all_items_in_cart[key].product_id;
      _obj["image"] = all_items_in_cart[key].image_link;
      _obj["price"] = all_items_in_cart[key].price;
      temp_items.push(_obj);
    }
    update_items_to_display(temp_items);
    console.log("temp_items", temp_items);
  }

  const calculateTotal = () => {
    if (items_to_display.length > 0) {
      return items_to_display.reduce((acc, item) => {
        return acc + item.price;
      }, 0);
    } else {
      return 0;
    }
  };

  const removeProduct = (id, updateCartPdtCount) => {
    for (let i = 0; i < items_to_display.length; i++) {
      if (items_to_display[i].childKey == id) {
        items_to_display.splice(i, 1);
        console.log("after deletion", items_to_display);
        update_items_to_display([...items_to_display]);
      }
    }
    let all_items_in_cart = localStorage.getItem("cart_items");
    console.log("cart", all_items_in_cart, typeof all_items_in_cart);
    if (
      all_items_in_cart != undefined &&
      all_items_in_cart != "" &&
      all_items_in_cart != "{}"
    ) {
      all_items_in_cart = JSON.parse(all_items_in_cart);
      console.log("after parse", all_items_in_cart);
      for (let key in all_items_in_cart) {
        console.log(key);
        if (key == id) {
          console.log("inside if");
          delete all_items_in_cart[id];
          console.log("items_in_the_cart after deletion", all_items_in_cart);
          localStorage.setItem("cart_items", JSON.stringify(all_items_in_cart));
          updateCartPdtCount({ count: Object.keys(all_items_in_cart).length });
        }
      }
    }
  };

  return (
    <>
      {items_to_display.length > 0 ? (
        <div>
          <ParentWrapper>
            {/* <Button color='blue' onClick={() => buy_now(update_items_to_display)}>Buy Now</Button> */}
            {/* <Button onClick={() => clear_cart(update_items_to_display)}>Clear Cart</Button> */}
            <ProductWrapper>
              <h3
                style={{ color: "#191e50" }}
              >{`Your cart (${items_to_display.length})`}</h3>
              {items_to_display.map((item) => (
                <div>
                  <CardWrapper>
                    <img src={item.image} height="120px" />
                    <DescriptionWrapper>
                      <DescriptionChildWrapper>
                        <div style={{ display: "flex", flex: 2.5 }}>
                          {item.title}
                        </div>
                        <div
                          style={{
                            display: "flex",
                            flex: 0.5,
                            justifyContent: "flex-end",
                          }}
                        >{`$ ${item.price}`}</div>
                      </DescriptionChildWrapper>
                      <HeaderConfigConsumer>
                        {(context) => {
                          const { updateCartPdtCount } = context[0];
                          return (
                            <ButtonWrapper
                              onClick={() =>
                                removeProduct(item.childKey, updateCartPdtCount)
                              }
                            >
                              REMOVE
                            </ButtonWrapper>
                          );
                        }}
                      </HeaderConfigConsumer>
                    </DescriptionWrapper>
                  </CardWrapper>
                </div>
              ))}
            </ProductWrapper>
            <div
              style={{
                background: "#fff",
                width: "25%",
                height: "210px",
                marginRight: "15%",
                marginLeft: "5%",
                padding: "20px",
              }}
            >
              <h3 style={{ color: "#191e50" }}>Total</h3>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  height: "25px",
                  alignItems: "center",
                  color: "#191e50",
                  fontFamily: "SourceSansPro-Regular",
                  fontSize: "14px",
                }}
              >
                <div>Subtotal</div>
                <div>$ {calculateTotal()}</div>
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  height: "30px",
                  alignItems: "center",
                  color: "#191e50",
                  fontFamily: "SourceSansPro-Regular",
                  fontSize: "14px",
                }}
              >
                <div>Delivery</div>
                <div>Free</div>
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  height: "30px",
                  alignItems: "center",
                  borderTopWidth: "1px",
                  borderTopColor: "black",
                  borderTopStyle: "solid",
                  marginTop: "5px",
                  color: "#191e50",
                  fontFamily: "SourceSansPro-Regular",
                  fontSize: "14px",
                  fontWeight: "bold",
                }}
              >
                <div>Total (VAT included)</div>
                <div>$ {calculateTotal()}</div>
              </div>
              <StyledLink onClick={() => buy_now()}>BUY NOW</StyledLink>
            </div>
          </ParentWrapper>
          <DeliveryWrapper>
            <h3>Estimated delivery</h3>
            <p>June 15, 2020 - June 22, 2020</p>
          </DeliveryWrapper>
        </div>
      ) : (
        <NoItemsMessage>Your cart is empty</NoItemsMessage>
      )}
    </>
  );
};

const ProductRecommendationSection = (props) => {
  const RecommendedWidgetsList = useRef({});
  const AIStylePicksList = useRef({});

  return (
    <ConfigConsumer>
      {(context) => {
        const { user_id, discover } = context;
        let API_KEY = context.API_KEY;
        let aiStyleEnabled = false;
        if (discover && discover.aiStyleEnabled) {
          aiStyleEnabled = true;
        }
        let url = context.url;
        if (discover.API_KEY) {
          API_KEY = discover.API_KEY;
        }
        let userId = localStorage.getItem("userId");
        if (!userId) {
          userId = user_id;
        }
        let carouselTobeRendered = [1];
        let aiStylePickToBeRendered = [20];
        const mad_UUID = getCookie("mad_UUID");
        const recommendation =
          discover && discover.recommendation
            ? discover.recommendation
            : url + "/widgets";
        if (API_KEY && userId && mad_UUID && recommendation) {
          RecommendedWidgetsList.current = {
            widgetList: ["7", "1", "11", "3"],
            postBody: `api_key=${API_KEY}&user_id=${userId}&cp_ensemble=true&details=true&duplicates=true&fields=["id","product_id","category","ontology","link","image_link","internal_image_url","small_image_link"]&mad_uuid=${mad_UUID}&num_results=[50]&require_source=true&widget_list=[7,1,11,3]`,
            recommendationUrl: recommendation,
          };
          if (aiStyleEnabled) {
            AIStylePicksList.current = {
              widgetList: ["20"],
              postBody: `api_key=${API_KEY}&user_id=${userId}&cp_ensemble=true&details=true&duplicates=true&fields=["id","product_id","category","ontology","link","image_link","internal_image_url","small_image_link"]&mad_uuid=${mad_UUID}&num_results=[5]&require_source=true&widget_list=[20]`,
              recommendationUrl: recommendation,
            };
          } else {
            AIStylePicksList.current = {};
          }
        } else {
          RecommendedWidgetsList.current = {};
          AIStylePicksList.current = {};
        }
        return (
          <div>
            <RecommendedPdtsContainer
              recommendedWidgetList={RecommendedWidgetsList.current}
            >
              <RecommendedPdtsConsumer>
                {(context) => {
                  const { loading } = context;
                  const CarouselTitle =
                    HeaderVueContent && HeaderVueContent.discover
                      ? HeaderVueContent.discover
                      : {};
                  return carouselTobeRendered.map((listKey, key) => {
                    return !loading &&
                      context[listKey] &&
                      context[listKey].length ? (
                      <ProductRecommendationWrapper
                        key={`Home Carousel ${key}`}
                      >
                        <ProductRecommendationTitle>
                          {CarouselTitle[listKey]}
                        </ProductRecommendationTitle>
                        <ProductCarousel
                          list={context[listKey] || []}
                          hideMSDOntology={true}
                          sliderPerGroup={5}
                          loading={loading}
                          showBuyButton={false}
                          showWishlistButton={false}
                        />
                      </ProductRecommendationWrapper>
                    ) : (
                      <ProductRecommendationWrapper
                        key={`Home Carousel ${key}`}
                      >
                        <ProductCarousel loading={loading} sliderPerGroup={5} />
                      </ProductRecommendationWrapper>
                    );
                  });
                }}
              </RecommendedPdtsConsumer>
            </RecommendedPdtsContainer>
            <RecommendedPdtsContainer
              recommendedWidgetList={AIStylePicksList.current}
            >
              <RecommendedPdtsConsumer>
                {(context) => {
                  const { loading } = context;
                  const CarouselTitle = "AI Style Picks For You";
                  return aiStylePickToBeRendered.map((listKey, key) => {
                    if ("20" in context) {
                      const StyleLookArr = JSON.parse(
                        JSON.stringify(context[listKey])
                      );
                      let defaultLookContainer = [];
                      StyleLookArr.map((styles, key) => {
                        defaultLookContainer.push({
                          value: `Style-${key + 1}`,
                          index: key.toString(),
                        });
                        return styles;
                      });
                      return !loading &&
                        context[listKey] &&
                        context[listKey].length ? (
                        <ProductRecommendationWrapper
                          key={`Home Carousel ${key}`}
                        >
                          <ProductRecommendationTitle>
                            {CarouselTitle}
                          </ProductRecommendationTitle>
                          <ProductCarouselWithTabs
                            tabsContainer={defaultLookContainer}
                            activeIndex="0"
                            recommendedData={StyleLookArr}
                            sliderPerGroup={3}
                            loading={loading}
                          />
                        </ProductRecommendationWrapper>
                      ) : (
                        <ProductRecommendationWrapper
                          key={`Home Carousel ${key}`}
                        >
                          <ProductCarousel
                            loading={loading}
                            sliderPerGroup={5}
                          />
                        </ProductRecommendationWrapper>
                      );
                    }
                  });
                }}
              </RecommendedPdtsConsumer>
            </RecommendedPdtsContainer>
          </div>
        );
      }}
    </ConfigConsumer>
  );
};

export { RenderCartItems, ProductRecommendationSection };
